import React from 'react'
import styles from './Cell.module.scss'
import { observer } from 'mobx-react'
import { useGrid } from '../../contextProviders/GridProvider'
import classNames from 'classnames'

const CellFactory = observer(({ id }: { id: string }) => {
  const
    { getCell } = useGrid(),
    cell = getCell(id)

  const
    { content, isHello, isEmoji, isText } = cell.attributes

  let child

  if (isEmoji) {
    if (isHello) {
      child = <Emoji content={content!} className={styles.isHello}/>
    } else {
      child = <Emoji content={content!}/>
    }
  } else if (isText) {
    child = <Text content={content!}/>
  }

  return (
    <div className={styles.cell}>
      {child}
    </div>
  )
})

interface IClassNameProps {
  className?: string
}

const Emoji: React.FC<ICellEmojiAttributes & IClassNameProps> = ({ content, className }) => (
  <div className={classNames(styles.cellEmoji, className)}>
    {content}
  </div>
)

const Text: React.FC<ICellTextAttributes> = ({ content }) => (
  <div className={styles.cellText}>
    {content}
  </div>
)

export default CellFactory
