import { action, computed, observable, reaction } from 'mobx'
import {
  calculateGridSize,
  getViewportSize,
  isMobile,
  makeFromCellScenario,
  makeSpiralScenario,
  runGridScenario
} from '../helpers/grid'
import { debounce } from 'lodash'
import { searchRandomEmoji } from '../helpers/emoji'
import { delay } from '../helpers/scenario'
import bind from '../decorators/bind'
import cellStyles from '../components/Cell/Cell.module.scss'

export enum EViews { initial, application, snake }

export class GridService {
  @observable
  columns: number = 0

  @observable
  rows: number = 0

  @observable.struct viewportSize = getViewportSize()
  @observable isMobile: boolean = false

  @observable
  private view: EViews = EViews.initial

  @observable
  grid: string[][] = []

  @observable
  private cells = new Map<string, ICell>()

  private menuPosition: IGridCell = {
    column: 0,
    row: 0
  }

  private pagePosition: IGridCell = {
    column: 0,
    row: 0
  }

  @action
  private initGrid() {
    let result: string[][] = []

    this.cells.clear()

    for (let row = 0; row < this.rows; row++) {
      result.push([])

      for (let column = 0; column < this.columns; column++) {
        const cellId = `${row}-${column}`

        result[row].push(cellId)

        this.cells.set(cellId, {
          attributes: {}
        })
      }
    }

    this.grid = result
  }

  @action
  private async init() {
    if (this.isAnimationRun) {
      this.initWasRequested = true
      return
    }

    const { columns, rows } = calculateGridSize(this.viewportSize)

    this.columns = columns
    this.rows = rows

    this.initGrid()

    if (!this.initialAnimationWasShown) {
      await delay(1000)
      this.showInitialAnimation()
    }
  }

  private initialAnimationWasShown = false

  private isAnimationRun = false
  private initWasRequested = false

  @action
  private startAnimation() {
    this.isAnimationRun = true
  }

  @action
  private stopAnimation() {
    this.isAnimationRun = false

    if (this.initWasRequested) {
      this.init()
    }

    this.initWasRequested = false
  }

  @action
  async showInitialAnimation() {
    this.startAnimation()

    const
      { columns, rows, grid, isMobile } = this,
      CLEAR_OFFSET = 1,
      STEP_DELAY = isMobile ? 15 : 10,
      midCol = Math.floor(columns / 2),
      midRow = Math.floor(rows / 2),
      middleCellId = grid[midRow][midCol],
      middleCell = this.getCell(middleCellId),
      middleCellEmoji = 'âœ‹'

    {
      const
        scenarioConfig = { columns, rows },
        scenario = makeSpiralScenario(scenarioConfig)

      await runGridScenario(
        scenario,
        STEP_DELAY,
        (data) => {
          data.forEach(({ column, row }) => {
            const
              id = grid[row][column],
              cell = this.getCell(id)

            if (middleCellId === id) {
              applyCellEmoji(cell, middleCellEmoji)
            } else {
              applyCellEmoji(cell, searchRandomEmoji([
                'snow', 'happy', 'santa', 'gift', 'family', 'beer', 'coffee', 'cup tea', 'glass wine', 'celebration', 'orange fruit'
              ]))
            }
          })
        }
      )
    }

    this.initialAnimationWasShown = true

    await delay(300)

    {
      const clear = async (vectors: I2DVector[]) => {
        const
          scenarioConfig: IFromCellScenarioArguments = {
            columns, rows,
            cell: {
              column: midCol,
              row: midRow,
            },
            vectors,
            minColumn: CLEAR_OFFSET,
            minRow: CLEAR_OFFSET,
            maxColumn: columns - CLEAR_OFFSET,
            maxRow: rows - CLEAR_OFFSET,
          },
          scenario = makeFromCellScenario(scenarioConfig)

        await runGridScenario(scenario, STEP_DELAY * 2.5, (data) => {
          data.forEach(({ column, row }) => {
            const
              id = grid[row][column],
              cell = this.getCell(id)

            if (middleCellId !== id) {
              clearCell(cell)
            }
          })
        })
      }

      !isMobile && await clear([
        { x: 1, y: 1 },
        { x: 1, y: -1 },
        { x: -1, y: 1 },
        { x: -1, y: -1 },
      ])

      await clear([
        { x: -1, y: 1 },
        { x: -1, y: -1 },
        { x: 0, y: -1 },
        { x: 1, y: -1 },
        { x: 1, y: 1 },
        { x: 0, y: 1 },
      ])
    }

    applyCellAttributes(middleCell, {
      isHello: true
    })

    let beforeLastAnimationTimestamp = performance.now()

    await delay(400)

    {
      const
        scenarioConfig = { columns, rows },
        scenario = makeSpiralScenario(scenarioConfig)

      await runGridScenario(
        scenario,
        20,
        (data) => {
          let stop = false

          data.forEach(({ column, row }) => {
            const
              id = grid[row][column],
              cell = this.getCell(id)

            clearCell(cell)

            stop = row === CLEAR_OFFSET && column === CLEAR_OFFSET - 1
          })

          if (stop) return true
        }
      )
    }

    const
      endDelay = parseInt(cellStyles.helloAnimationTime) - (performance.now() - beforeLastAnimationTimestamp)

    if (endDelay > 0) {
      await delay(endDelay)
    }

    clearCell(middleCell)

    this.stopAnimation()
  }

  @computed
  get square() {
    return this.columns * this.rows
  }

  @bind
  getCell(id: string) {
    return this.cells.get(id)!
  }

  constructor() {
    this.isMobile = isMobile()

    this.init()

    window.addEventListener('resize', debounce(() => {
      this.viewportSize = getViewportSize()
    }, 500))

    reaction(() => {
      return this.viewportSize
    }, () => {
      this.isMobile = isMobile()
      this.init()
    })
  }
}

const makeGrid = () => {
  return new GridService()
}

const applyCellAttributes = (cell: ICell, attributes: ICellAttributes, merge: boolean = true): void => {
  if (merge) {
    cell.attributes = {
      ...cell.attributes,
      ...attributes
    }
  } else {
    cell.attributes = {
      ...attributes,
    }
  }
}

const clearCell = (cell: ICell): void => {
  cell.attributes = {}
}

const applyCellEmoji = (cell: ICell, emoji: string): void => applyCellAttributes(cell, {
  isEmoji: true,
  content: emoji,
}, false)

export default makeGrid
